.main-menu-box {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(3px) grayscale(.3);
    color: white;
}

.stage-menu-box {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    width: 95%;
    height: 80%;
    background-color: rgba(0, 0, 0, 0.774);
    color: white;
}

.main-menu-title {
    position: absolute;
    left: 5%;
    top: -10%;
    width: auto;
    height: auto;
    font-size: 35px;
    z-index: 2;
}

.start-button {
    position: absolute;
    flex-direction: row;
    right: 0%;
    bottom: -8%;
    border: none;
    font-size: 30px;
    background: none;
    height: auto;
    width: auto;
    transition: transform 0.2s;
    outline: none;
    color:white;
}

.start-button:hover {
    transform: scale(1.2);
}

.start-button ion-icon {
    color: #3c40c6;
    font-size: 25px;
}