.back-div-filter {
  height: 100vh;
  width: 100vw;
  backdrop-filter: blur(2px) grayscale(.3);
  transition: .5s backdrop-filter;
}

.fade-enter {
  opacity: 0.01;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.fade-leave {
  opacity: 1;
}

.fade-leave.fade-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}